import { Component, HostListener, OnInit, ViewChild, ElementRef, Renderer2 } from '@angular/core';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {

  constructor(private renderer: Renderer2) { }

  navbar: HTMLCollectionOf<Element> = document.getElementsByClassName('navbar');
  sticky = null;
  title = 'mysite';
  i: number = 1;
  element: HTMLElement;
  el: HTMLElement;
  space = document.getElementById("home") as HTMLElement;


  ngOnInit(): void {
    this.space = document.getElementById("home") as HTMLElement;
    this.sticky = (this.navbar[0] as HTMLElement).offsetTop;



    /* ***** ** ***** Graphics Animation ***** ** ***** */
    /* -------------------------------------------------*/
    //1000 = 1second..
    setInterval(() => {
      let glxy = document.getElementById("home") as HTMLElement;

      //object initialized
      const div: HTMLDivElement = this.renderer.createElement('div');
      div.classList.add("comet");

      //object initial position
      let posx = 100;
      let posy = Math.floor(Math.random() * 430) + 10;
      div.style.left = posx + 'vw';
      div.style.top = posy + 'px';


      //render the object
      this.renderer.appendChild(glxy, div);


      //speed
      let x = Math.floor(Math.random() * 60) + 1;
      div.style.transition = x + 's';

      //object type
      let y = Math.floor(Math.random() * 10);
      div.classList.add("c" + (y + 1));

      //object size
      let size = Math.floor(Math.random() * 8) + 1;
      div.style.width = (size * 5) + 'px';
      div.style.height = (size * 5) + 'px';

      //move the object .25sec delay
      setTimeout(function () {
        div.style.left = '-200px';
      }, 250);

      //remove object after a minute
      setTimeout(function () {
        div.remove();
      }, 61000);

    }, 1000);

    setInterval(() => {
      this.space = document.getElementById("home") as HTMLElement;

      //create object
      const div: HTMLDivElement = this.renderer.createElement('div');
      div.classList.add("comet");
      div.setAttribute('id', "comet" + this.i);
      div.style.transition = '1s';

      //object initial position;
      let x = Math.floor(Math.random() * 1300) + 50;
      let y = Math.floor(Math.random() * 400) + 30;
      div.style.left = x + 'px';
      div.style.top = y + 'px';

      //size and type of object
      let size = Math.floor(Math.random() * 40) + 1;
      let choice = Math.floor(Math.random() * 4);
      div.style.width = size + 'px';
      div.style.height = size + 'px';
      div.classList.add("c" + (choice + 1));

      //render the object
      this.renderer.appendChild(this.space, div);

      //1sec delay for animation
      setTimeout(function () {
        //move the object
        this.el = document.getElementById(div.getAttribute('id')) as HTMLElement;
        let x = Math.floor(Math.random() * 1300) + 50;
        let y = Math.floor(Math.random() * 400) + 30;
        this.el.style.left = x + 'px';
        this.el.style.top = y + 'px';
      }, 1000);
      //1sec delay for animation
      setTimeout(function (r: Renderer2) {
        //cut the object
        try {
          div.classList.add("off");
          //r.removeChild(this.space, document.getElementById(div.getAttribute('id'))); 
        } catch (error) {
          console.log(error);
        }
      }, 1000);


      //remove object after 8sec
      setTimeout(function () {
        div.remove();
      }, 8000);
      this.i = this.i + 1;
    }, 4000);

    /* ***** ** ***** Graphics Animation ***** ** ***** */
    /* -------------------------------------------------*/


    //insert code here..

  }


  //sticky navigation bar trigger
  @HostListener('window:scroll') onScroll() {
    if (this.navbar.length < 2) {
      if (window.pageYOffset >= this.sticky) {
        this.navbar[0].classList.add("sticky")
      } else {
        this.navbar[0].classList.remove("sticky");
      }
    }
  }



}
